.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  text-align: center;
  padding-bottom: 15px;
}

input {
  font: inherit;
  border-radius: 4px;
  padding: 8px;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  color: #00f;
  background-color: #dadaff;
  border: 1px solid #dadaff;
  border-radius: 4px;
  padding: 8px 12px;
  font-weight: bolder;
  transition: background-color .2s linear, color .2s linear;
}

button:hover, button:focus {
  color: #dadaff;
  background-color: #00f;
}

.search__form {
  text-align: center;
  background-color: #00f;
  padding-top: 15px;
  padding-bottom: 15px;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.gallery {
  color: #000;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  margin-left: 15px;
  margin-right: 15px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 14px;
  display: flex;
}

.gallery__item {
  width: 281px;
  height: 220px;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image {
  width: 100%;
  height: 160px;
  object-fit: cover;
  transition: transform .3s linear;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__link {
  height: 100%;
  color: inherit;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.info__item {
  text-align: center;
}

.info__data {
  text-align: center;
  display: block;
}

